import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';

import { Approval } from '../../classes/approval';
import { ApprovalService } from '../../angular-services/approval.service';
import { Page } from '../../classes/page';
import { PagerService } from '../../angular-services/pager.service';

@Component({
  selector: 'app-alunos-aprovados',
  templateUrl: './alunos-aprovados.component.html',
  styleUrls: ['./alunos-aprovados.component.css']
})
export class AlunosAprovadosComponent implements OnInit {

  @Output() loading = new EventEmitter<boolean>();
  @Input('amountApprovals') amountApprovals: number;
  @Input('amountPerPage') amountPerPage: number;
  showPages: boolean = true;
  approvals: Approval[] = [];
  pageSelected: Page = new Page();
  pages: Page[] = [];

  constructor(private approvalService: ApprovalService, private pagerService: PagerService) { }

  ngOnInit() {
    this.getAlunos();
  }

  verifyPages() {
    if ((this.amountApprovals - this.amountPerPage) > 0) {
      this.showPages = true;
    } else {
      this.showPages = false;
    }
  }

  getAlunos() {
    this.approvalService.getApprovals().subscribe(
      approvals => {
        this.approvals = approvals;
        this.pages = this.pagerService.fillPages(approvals, this.amountPerPage, this.amountApprovals);
        this.pageSelected = this.pages[0];
        this.verifyPages();
      }
    );
  }

  selectPage(page: Page){
    this.pageSelected = page;
  }

}
